<template>
  <div class="wrapper">
    <nav class="nav">
      <h1 class="nav__title">github finder</h1>
    </nav>
    <main class="main">
      <div class="main__input">
        <input type="text" placeholder="Введите имя пользователя" v-model="name">
        <button class="main__btn" 
        @click="linker(name),name='',getError"  >НАЙТИ</button>
      </div>
      <div class="main__info" v-show="view">
        <div class="main__info-left">
          <img :src="getAvatar" alt="">
          <a class="main__info-btn" :href="getURL">Посетить</a>
        </div>
        <div class="main__info-right">
          <h2 class="main__right-name">{{ getName }}</h2>
          <h2 class="main__right-repos">Репозиториев: {{ getRepos2 }}</h2>
          <h2 class="main__right-created">Создан: {{getCreationDate}}</h2>
          <h2 class="main__right-followers">Подписщиков: {{ getFollowers }}</h2>
          <h2 class="main__right-follows">Подписок: {{ getFollowing }}</h2>
        </div>
      </div>
      <div class="main__contentik" v-show="view">
        <div class="main__content-item" v-for=" (repo,i) in repos" :key="i">
            <div class="main__content-item-chap">
                <h3 class="main__content-item-text">{{
                  repo.name
                 }}</h3>
                <p class="main__content-item-p">Кол-во звёзд: {{repo.stargazers_count }}</p>
                <p class="main__content-item-p">Дата добавления: {{ repo.updated_at.split('T')[0] }}</p>
            </div>
            <div class="main__content-item-on">
                <a :href="repo.html_url" class="main__content-item-btn">ПОСЕТИТЬ</a>
            </div>
        </div>
        
    </div>
    <div class="main__error" v-show="error">
      <h1 class="main__error-title">Произошла ошибка </h1>
    </div>
    </main>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
  export default {
    data() {
      return {
        name: '',
        view: false,
        error:false,
        
      };
    },
    computed:{
      ...mapState(['info']),
      ...mapState(['repos']),
      
      getName(){
        if(this.info != ''){
          return this.info.data.login
          
        }
      },
      getRepos2(){
          if(this.info != ''){
            return this.info.data.public_repos
          }
        },
        getCreationDate(){
          if(this.info != ''){
            return this.info.data.created_at.split('T')[0]
            
          }
        },
        getFollowers(){
          if(this.info != ''){
            return this.info.data.followers
          }
        },
        getFollowing(){
          if(this.info != ''){
            return this.info.data.following
          }
        },
        getURL(){
          if(this.info != ''){
            return this.info.data.html_url
          }
        },
        getAvatar(){
          if(this.info != ''){
            return this.info.data.avatar_url
          }
        },
        getReposName(){
          if(this.repos != null){
            
            for (let i = 0; i <= this.getRepos2; i++) {
              let res =this.repos.data.filter(name => name.name)
              return res[i++].name
              
            }  
              
            }
          },
          getError(){
            
              setTimeout(() => {
                if(this.info == ''){
                this.error = true
              }else{
                this.view = true
              }
              }, 1000);
            
          },
          
        }
    ,
    methods: {
      ...mapActions(['getInfo']),
      ...mapActions(['getRepos']),
      
      linker(name){
        if(this.name != ''){
        this.getInfo(name)
        this.getRepos(name)
        }
      },
      oPen(){
        if(this.getInfo == ''){
          this.view = false
        }else{
          this.view = true
        }
      }
    }
    
  }
</script>

