import axios from 'axios'
import { createStore } from 'vuex'

export default createStore({
  state: {
    info:'',
    repos:'',
    
  },
  getters: {
    
  },
  mutations: {
    getInfo(state, payload){
      state.info = payload
    },
    getRepos(state, payload) {
      state.repos  =payload
    },
    
    
  },
  actions: {
    
      async getInfo(context,name){
        let response = await axios.get(`https://api.github.com/users/${name}`)
        let res2 = {...response}
        context.commit('getInfo',res2)
        console.log(res2);
      },
      async getRepos(context,name) {
        let response = await axios.get(`https://api.github.com/users/${name}/repos`)
        let res2 ={...response.data}
        console.log(res2);
        context.commit('getRepos',res2)
      }
      
    
  }
  
})
